
body{margin:0;background-color:#e1e1e1
}
img{vertical-align:initial
}
.detail-body{width:90%;min-height:calc(100vh - 100px);margin:10px auto
}
@media only screen and (max-width: 950px){
.detail-body .lift-content{z-index:16;width:100% !important
}
.detail-body .right-content{visibility:hidden !important;width:0px !important
}
}
.detail-body .lift-content{float:left;width:calc(100% - 300px)
}
.detail-body .lift-content .box-card{margin-bottom:10px
}
.detail-body .lift-content .box-card .el-card__header{padding:8px 20px;padding-bottom:0
}
.detail-body .lift-content .box-card .el-card__header .card-title .el-page-header{height:40px;line-height:40px
}
.detail-body .lift-content .box-card .el-card__header .card-title .el-page-header .el-page-header__content{font-size:14px
}
.detail-body .lift-content .box-card .el-card__header .card-replies-title{height:40px;line-height:40px;font-size:14px
}
.detail-body .lift-content .box-card .el-card__body{padding-top:0px
}
.detail-body .lift-content .box-card .el-card__body .my-topic-title .topic-title{float:left;width:100%;border-bottom:1px solid #e5e5e5;margin-bottom:8px
}
.detail-body .lift-content .box-card .el-card__body .my-topic-title .topic-title .title-left{width:calc(100% - 40px);float:left
}
.detail-body .lift-content .box-card .el-card__body .my-topic-title .topic-title .title-left .topic-title-tab{padding-top:4px;width:80px;float:left
}
.detail-body .lift-content .box-card .el-card__body .my-topic-title .topic-title .title-left .title-name{width:calc(100% - 80px);font-size:20px;font-weight:bold;float:right
}
.detail-body .lift-content .box-card .el-card__body .my-topic-title .topic-title .title-left .topic-title-desc{clear:both;font-size:12px;color:#838383;margin-bottom:14px
}
.detail-body .lift-content .box-card .el-card__body .my-topic-title .topic-title .title-right{width:40px;float:right
}
.detail-body .lift-content .box-card .el-card__body .my-topic-title .topic-title .topic-content{clear:both
}
.detail-body .lift-content .box-card .el-card__body .replie-item{border-bottom:1px solid #e5e5e5;margin-bottom:8px
}
.detail-body .lift-content .box-card .el-card__body .replie-item .replie-user-img{margin:5px;width:40px;height:40px;float:left
}
.detail-body .lift-content .box-card .el-card__body .replie-item .replie-title{width:calc(100% - 50px);float:left;font-size:18px;font-weight:bold;height:30px;line-height:30px
}
.detail-body .lift-content .box-card .el-card__body .replie-item .replie-desc{width:calc(100% - 50px);float:left;font-size:12px;color:#b4b4b4
}
.detail-body .lift-content .box-card .el-card__body .replie-item .replie-content{clear:both
}
.detail-body .right-content{float:right;width:290px
}
.detail-body .right-content .box-card{margin-bottom:10px
}
.detail-body .right-content .box-card .el-card__header{padding:8px 20px;padding-bottom:0
}
.detail-body .right-content .box-card .el-card__header .card-title{height:40px;line-height:40px;font-size:14px
}
.detail-body .right-content .box-card .author{height:40px
}
.detail-body .right-content .box-card .author .author-img{width:40px;margin-right:10px;float:left
}
.detail-body .right-content .box-card .author .author-name{width:calc(100% - 50px);font-size:18px;float:left
}
.detail-body .right-content .box-card .client-box{width:100%
}
.detail-body .right-content .box-card .client-box .el-link{display:inline-block;width:100%;text-align:center
}
